import React, { useState } from "react";

const CardSkeleton = (props) => {
  const [skeleton, setrmskeleton] = useState(true);

  setTimeout(() => {
    setrmskeleton(false);
  }, 3000);

  return (
    <div className="card">
      <div id="image" className={skeleton ? "cardImageLoading" : "cardImage"}>
        <img src={props.image} alt="" />
      </div>
      <div className={skeleton ? "cardTitleLoading" : "cardTitle"}>
        <p>{props.title}</p>
      </div>
      <div className={skeleton ? "cardDescLoading" : "cardDesc"}>
        <p>{props.desc}</p>
      </div>
    </div>
  );
};

export default CardSkeleton;
